$gps-burg:           rgba(182, 15, 97, 0.9);
$gps-orange:         rgba(242, 112, 45, 0.9);
$gps-skyblue:        rgba(45, 181, 167, 0.9);
$gps-purple:         rgba(190, 61, 244, 0.9);
$gps-green:          rgba(180, 224, 67, 0.9);
$gps-yellow:         rgba(242, 194, 58, 0.9);
 
 


.Details{
    position: relative;
    top: 100px;
    span {
        text-transform: uppercase;  
      }
      .container {
        width: 800px;
        height: 420px;
        padding: 10px;
        margin: 0 auto;
        background: whitesmoke;
        position: relative;
        h1 {
            position:relative;
            top: 200px;
            text-align: center;
            color: $gps-burg;
            font-size: 3.5em;
          }
        .balloon { 
            width: 738px;
            margin: 0 auto;
            padding-top: 30px;
            position: relative;
          }
          .balloon > div {
             width:104px;
            height:140px; 
            background: $gps-burg;
            border-radius:0;
            border-radius:80% 80% 80% 80%;
            margin: 0 auto;
            position: absolute;
            padding:10px;
            box-shadow:inset 17px 7px 10px $gps-burg; 
            -webkit-transform-origin: bottom center; 
          }
          .balloon > div:nth-child(1){
            background: $gps-burg;
            left: 0; 
            box-shadow:inset 10px 10px 10px darken($gps-burg, 10%);
            -webkit-animation:balloon1 6s ease-in-out infinite;
            -moz-animation:balloon1 6s ease-in-out infinite;
            -o-animation:balloon1 6s ease-in-out infinite;
            animation:balloon1 6s ease-in-out infinite;
            &:before { 
             color: $gps-burg ;
            }
          
          }
          .balloon > div:nth-child(2){
            background: $gps-orange;
            left: 120px; 
            box-shadow:inset 10px 10px 10px darken($gps-orange, 10%);
             -webkit-animation:balloon2 6s ease-in-out infinite;
            -moz-animation:balloon2 6s ease-in-out infinite;
            -o-animation:balloon2 6s ease-in-out infinite;
            animation:balloon2 6s ease-in-out infinite;
            &:before { 
             color: $gps-orange ;
            }
          
          }
          .balloon > div:nth-child(3){
            background: $gps-skyblue;
            left: 240px; 
            box-shadow:inset 10px 10px 10px darken($gps-skyblue, 10%);
             -webkit-animation:balloon4 6s ease-in-out infinite;
            -moz-animation:balloon4 6s ease-in-out infinite;
            -o-animation:balloon4 6s ease-in-out infinite;
            animation:balloon4 6s ease-in-out infinite;
            &:before { 
             color: $gps-skyblue ;
            }
          
          }
          .balloon > div:nth-child(4){
            background: $gps-purple;
            left: 360px; 
            box-shadow:inset 10px 10px 10px darken($gps-purple, 10%); 
             -webkit-animation:balloon1 5s ease-in-out infinite;
            -moz-animation:balloon1 5s ease-in-out infinite;
            -o-animation:balloon1 5s ease-in-out infinite;
            animation:balloon1 5s ease-in-out infinite;
            &:before { 
             color: $gps-purple ;
            }
          
          }
          .balloon > div:nth-child(5){
            background: $gps-green;
            left: 480px; 
            box-shadow:inset 10px 10px 10px darken($gps-green, 10%);
             -webkit-animation:balloon3 5s ease-in-out infinite;
            -moz-animation:balloon3 5s ease-in-out infinite;
            -o-animation:balloon3 5s ease-in-out infinite;
            animation:balloon3 5s ease-in-out infinite;
            &:before { 
             color: $gps-green ;
            }
          
          }
          .balloon > div:nth-child(6){
            background: $gps-yellow;
            left: 600px; 
            box-shadow:inset 10px 10px 10px darken($gps-yellow, 10%);
            -webkit-animation:balloon2 3s ease-in-out infinite;
            -moz-animation:balloon2 3s ease-in-out infinite;
            -o-animation:balloon2 3s ease-in-out infinite;
            animation:balloon2 3s ease-in-out infinite;
            &:before { 
             color: $gps-yellow ; 
            }
          
          }
          .balloon > div:before {
            color:$gps-burg;
            position:absolute;
            bottom:-11px;
            left: 52px;  
            content:"â–²";
            font-size:1em;
          }
      }
     
      
      span {
        font-size: 4.8em;
        color: white;
        position: relative;
        top: 30px;
        left: 50%;
        margin-left: -27px; 
      } 
      
      /*BALLOON 1 4*/
      @keyframes balloon1 {
        0%,100%{ -webkit-transform:translateY(0) rotate(-6deg);}
        50%{ -webkit-transform:translateY(-20px) rotate(8deg); }
      }
      
      
      /* BAllOON 2 5*/
      @keyframes balloon2 {
        0%,100%{ -webkit-transform:translateY(0) rotate(6eg);}
        50%{ -webkit-transform:translateY(-30px) rotate(-8deg); }
      }
   
      /* BAllOON 0*/
      @keyframes balloon3 {
        0%,100%{ -webkit-transform:translate(0, -10px) rotate(6eg);}
        50%{ -webkit-transform:translate(-20px, 30px) rotate(-8deg); }
      }
      
      /* BAllOON 3*/
      @keyframes balloon4 {
        0%,100%{ -webkit-transform:translate(10px, -10px) rotate(-8eg);}
        50%{ -webkit-transform:translate(-15px, 20px) rotate(10deg); }
      } 
     
      
}